import React, { Component } from "react";
import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { withRouter } from "react-router";
/*
 Custom Modules exported here
 */
import Navbar from "./Components/Navbar";
import TransactionHomepageEntities from "./Components/TransactionHomepageEntities";
import TodoHome from "./Components/TodoHome";
import MoneyGiven from "./Components/InternalRoutes/MoneyGivenRoute/MoneyGiven";
import MoneyToTake from "./Components/InternalRoutes/MoneyToTakeRoute/MoneyToTake";
import ImportantTransaction from "./Components/InternalRoutes/ImportantTransactionsRoute/ImportantTransaction";
import SettledTransaction from "./Components/InternalRoutes/SettledTransactionsRoute/SettledTransaction";
import ComponentTester from "./Components/ComponentTester";

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			userTransactionsInfo: [],
			showModalUserFields: false,
			isModalContainerVisible: false,
			theme: {
				dark: {
					background: '#0e0e0e url("./images/body-bg.png") center;',
					navColor: " #1a1a1a",
					color: "#1a1a1a",
					boxShadow: "5px 5px 25px 0 rgba(255,255,255,0.2)",
					addUserBtnColor: "#fff",
				},
				light: {
					navColor: "#fff",
					color: "#fff",
					boxShadow: "5px 5px 25px 0 rgba(46,61,73,.2)",
					backgroundSize: "cover",
					addUserBtnColor: "var(--green)",
				},
			},
			selectedTheme: "light",
			showNavbarMoreOptions: false,
		};

		this.toggleShowModalUserFields = this.toggleShowModalUserFields.bind(this);
		this.toggleModalContainerVisibility = this.toggleModalContainerVisibility.bind(
			this
		);
		this.toggleAppThemeChange = this.toggleAppThemeChange.bind(this);
		this.toggleShowNavbarMoreOptions = this.toggleShowNavbarMoreOptions.bind(
			this
		);
	}
	//This function will trigger a state change when the User name select drop down changes its value.
	//That is when the user will select Add New User from the dropdown in the AddTransactionModal
	//then this function will be called.
	//This function will change showModalUserFields:false => showModalUserFields:true and hence
	//display the complete form in the modal to enter information.

	toggleShowModalUserFields(dropdownValue) {
		console.log(dropdownValue);
		if (dropdownValue == "add_user") {
			this.setState({
				showModalUserFields: true,
			});
		} else {
			this.setState({
				showModalUserFields: false,
			});
		}
	}
	toggleModalContainerVisibility(isVisible) {
		this.setState({
			isModalContainerVisible: isVisible,
		});
	}
	toggleAppThemeChange(themeValue) {
		this.setState({
			selectedTheme: themeValue ? "dark" : "light",
		});
		if (themeValue) {
			document.body.className = "dark-theme-body";
		} else {
			document.body.className = "light-theme-body";
		}
	}
	componentDidMount() {
		document.body.className = "light-theme-body";
	}
	toggleShowNavbarMoreOptions(showNavbar) {
		this.setState({
			showNavbarMoreOptions: showNavbar,
		});
	}

	render() {
		return (
			<Router>
				<div>
					<Navbar
						toggleAppThemeChange={this.toggleAppThemeChange}
						selectedTheme={this.state.selectedTheme}
						theme={this.state.theme[this.state.selectedTheme]}
						toggleShowNavbarMoreOptions={this.toggleShowNavbarMoreOptions}
						showNavbarMoreOptions={this.state.showNavbarMoreOptions}
					/>
					<Route
						exact
						path="/"
						component={() => <TodoHome theme={this.state.theme} />}
					/>
					<Route
						path="/todo-money"
						component={() => <TodoHome theme={this.state.theme} />}
					/>
					<Route
						path="/todo-home"
						component={() => <TodoHome theme={this.state.theme} />}
					/>
					<Route
						path="/money-given"
						component={() => (
							<MoneyGiven
								isModalContainerVisible={this.state.isModalContainerVisible}
								showModalUserFields={this.state.showModalUserFields}
								toggleShowModalUserFields={this.toggleShowModalUserFields}
								toggleModalContainerVisibility={
									this.toggleModalContainerVisibility
								}
							/>
						)}
					/>
					<Route path="/money-to-take" component={MoneyToTake} />
					<Route
						path="/important-transactions"
						component={ImportantTransaction}
					/>
					<Route path="/settled-transactions" component={SettledTransaction} />
					<Route
						path="/component-test"
						component={() => (
							<ComponentTester
								theme={this.state.theme}
								selectedTheme={this.state.selectedTheme}
							/>
						)}
					/>
				</div>
			</Router>
		);
	}
}

console.log(new App());
export default App;



