import React, { useState, useEffect } from "react";
import "./AddTransactionModal.css";
import AddTransactionIcon from "../../images/add-transaction-icon.png";

class UserTransactionData_Main {
	constructor(...data) {
		(this.name = data[0]),
			(this.amount = data[1]),
			(this.dateOfUserAdded = data[2]),
			(this.description = data[3]),
			(this.transactionHistory = []),
			(this.isDeleted = false),
			(this.notifyUser = false),
			(this.notifyUserCount = 3),
			(this.importantTransaction = []),
			(this.isSettled = false);
	}
}
class UserTransactionData_History_Thread extends UserTransactionData_Main {
	constructor(...data) {
		super(...data);
		(this.isImportant = false),
			(this.dateOfTransaction =
				new Date().getDate() +
				"-" +
				new Date().getMonth() +
				"-" +
				new Date().getFullYear());
	}
}

function AddTransactionModal(props) {
	const { isModalContainerVisible, toggleModalContainerVisibility } = props;
	const [name, setName] = useState("");
	const [amount, setAmount] = useState(0);
	const [date, setDate] = useState("");
	const [description, setDescription] = useState("");
	const [userState, setUserState] = useState([]);

	const modalCurrentStateClass = {
		true: "d-flex",
		false: "d-none",
	};

	return (
		<div
			className={
				"modal-container-bg " + modalCurrentStateClass[isModalContainerVisible]
			}
		>
			<div className="modal-container my-5">
				<div className="modal-container__modal-header p-3 border-bottom d-flex justify-content-between align-items-center">
					<div className="modal-head-title d-flex justify-content-center align-items-center">
						<h5 className="modal-title font-weight-bold">Add Transaction</h5>
						<img
							className="modal-icon"
							src={AddTransactionIcon}
							alt="Add transaction icon"
						/>
					</div>

					<div className="close-theme-toggler-container-header ">
						<button
							type="button"
							className="close"
							onClick={() => toggleModalContainerVisibility(false)}
						>
							<span className="font-weight-light modal-cross-close-btn">
								&times;
							</span>
						</button>
					</div>
				</div>
				<div className="modal-container__modal-body p-3">
					<div className="d-block">
						<h1 className="modal-form-label ">Name:</h1>
						<input
							onChange={(e) => setName(e.target.value)}
							className="form-control  "
							type="text"
							placeholder="Name"
						/>
						<h1 className="modal-form-label mt-3">Amount:</h1>
						<input
							className="form-control w-50 "
							type="number"
							onChange={(e) => setAmount(e.target.value)}
							placeholder="Amount"
						/>
						<h1 className="modal-form-label mt-3">Date:</h1>
						<input
							className="form-control w-50 "
							type="date"
							onChange={(e) => setDate(e.target.value)}
							placeholder="Date of Transaction"
						/>
						<h1 className="modal-form-label mt-3">Description:</h1>
						<textarea
							className="form-control"
							type="number"
							onChange={(e) => setDescription(e.target.value)}
							placeholder="Amount"
						></textarea>
					</div>
				</div>
				<div className="modal-container__modal-footer d-flex justify-content-end w-100  p-3">
					<button
						type="button"
						onClick={() => toggleModalContainerVisibility(false)}
						className="text-white py-2 px-3 mr-xl-3 mr-lg-3 mr-md-3 mr-sm-3 mr-2 modal-footer-close-btn"
					>
						Close
					</button>
					<button
						type="button"
						onClick={() =>
							setUserState((userState) => [
								...userState,
								new UserTransactionData_Main(name, amount, date, description),
							])
						}
						className="text-white py-2 px-3 modal-footer-add-btn"
					>
						Add Transaction
					</button>
				</div>
			</div>
		</div>
	);
}

export default AddTransactionModal;



